<template>
    <div class="tab-bar-item" v-on:click="itemclick()" v-bind:class="{active:isActive}">
        <div v-if="!isActive"><slot name="sidebar-item-icon"></slot></div>
        <div v-else><slot name="item-icon-active"></slot></div>
        <div><slot name="sidebar-item-text"></slot></div>
    </div>
</template>

<script>
    export default {
        name: "SidebarItem",
        props: {
            path: String,
        },
        data() {
          return {

              }
          },
        computed: {
            isActive() {
                // 拿到当前活跃的route对象，在这个对象里拿到path，当前只有一个对象活跃，只能拿到一个path，跟这个组件里的path对比，
                // 如果对上了，就能返回
                return this.$route.path.indexOf(this.path) !== -1
            }
        },
        methods: {
            itemclick() {
                // eslint-disable-next-line no-console
                //  console.log("itemclick");
                this.$router.push(this.path);
            },
        },
    }
</script>

<style scoped>
    .tab-bar-item {
        cursor: pointer;
        color: lightgrey;
        height: 15px;
        width: 150px;
        padding: 15px;
    }
    .tab-bar-item:hover {
        color: lightgoldenrodyellow;
    }
    .active {
        background: #535458;
        color: lightgoldenrodyellow;
        border-radius: 2px;
    }
</style>
